export default {
	data() {
		return {
			// 默认用户是未登录状态
			userIsLogin: false,
			userInfo: {}
		}
	},
	methods: {

	},
	// 页面显示时候的生命周期函数
	onShow(){
		// 用户状态的切换
		let userInfo = this.getGlobalUser("userInfo");
		if(userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo
		}else {
			this.userIsLogin = false;
			this.userInfo = {};
		}
	}
}
